.voice-cards--layout {
  display: grid;
  gap: 2.5rem;
  grid-template-columns: 100%;
}
@media screen and (min-width: 768px) {
  .voice-cards--layout {
    grid-template-columns: 1fr 1fr;
    gap: 5rem 2.5rem;
  }
}

.voice__btn {
  margin-top: 2.4375rem;
}
@media screen and (min-width: 768px) {
  .voice__btn {
    margin-top: 5.0625rem;
  }
}

.js-fadeUp {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s, transform 0.8s;
}

.js-fadeUp.is-inview {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.3s;
}

.js-fadeLeft {
  opacity: 0;
  visibility: hidden;
  transform: translateX(80px);
  transition: opacity 1s, visibility 1s, transform 1s;
}

.js-fadeLeft.inview {
  opacity: 1;
  visibility: visible;
  transform: translateX(0px);
}

.js-mv-swiper,
.swiper-slide picture,
.swiper-slide img {
  height: inherit;
  height: 100%;
  width: 100%;
}

.swiper-slide img {
  object-fit: cover;
  filter: brightness(80%);
}

.swiper-slide.campaign__slide {
  height: auto;
}

.campaign-card__img img {
  filter: none;
}

.swiper-button-next,
.swiper-button-prev {
  position: initial;
  width: 5rem;
  height: 5rem;
  margin-top: 0;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  content: "";
  transition: all 0.3s ease 0s;
}
@media screen and (min-width: 768px) {
  .swiper-button-next::after,
  .swiper-button-prev::after {
    width: 5rem;
    height: 5rem;
  }
}

@media (any-hover: hover) {
  .swiper-button-next:hover::after,
  .swiper-button-prev:hover::after {
    opacity: 0.5;
  }
}
.js-campaign-swiper {
  padding-bottom: 0.625rem;
  padding-left: 0.375rem;
  margin-left: -0.375rem;
}

.swiper-button-next::after {
  background: transparent url(../images/common/next.png) no-repeat center/contain;
}

.swiper-button-prev::after {
  background: transparent url(../images/common/prev.png) no-repeat center/contain;
}

.campaign__button-group {
  display: flex;
  gap: 5rem;
  flex-direction: row-reverse;
  top: 0;
  right: calc(50% - min(48%, 37.5rem));
  position: absolute;
}