@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP", sans-serif;
$second-font-family: "Noto Serif JP", serif;
$third-font-family: "Gotu", sans-serif;
$fourth-font-family: "Lato", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
  position: relative;
}

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #000;
$white: #fff;
$gray: #F2F2F2;
$gray2: #202020;
$gray3: #D9D9D9;
$gray4: #9A9A9A;
$gray5: #F4F4F4;
$green: #0D2936;
$green2: #DDF0F1;
$green-accent: #408F95;
$red: #C94800;
$redbrick: #B55A3E;
$deepskyblue: #2C6D9D;
